.profile__avatar {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  overflow: hidden;
  object-fit: cover;
  cursor: pointer;
  transition: 0.3s ease-out;
}

.profile__avatar:hover {
  opacity: 0.2;
}

@media screen and (max-width: 767px) {
  .profile__avatar {
    margin: 0 0 26px;
  }
}
